.home-wrapper {
  .product-choose {
    padding: 20px 0;
    background-color: lighten($ast-brand-primary, 10%);
    h2 {
      text-transform: uppercase;
      margin: 0;
    }
    .btn {
      background-color: $moton-brand-primary;
    }
  }

  .product-nav {
    // display: grid;
    // grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    // grid-auto-rows: 250px;
    // grid-gap: 0;
    display: flex;
    flex-wrap: wrap;
  }

  .product-nav-item {
    position: relative;
    height: 200px;
    flex: 1;
    overflow: hidden;
    margin: 0;

    img {
      max-height: 100%;
    }

    figcaption {
      position: absolute;
      bottom: 0;
      font-size: 24px;
      text-transform: uppercase;

      left: 0;
      right: 0;
      text-align: center;
      background: rgba(0, 0, 0, 0.5);
      font-weight: 900;
      color: #fff;
      padding: 20px;
      transition: all 0.3s;
      min-height: 70px;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    &:hover {
      figcaption {
        min-height: 100%;
        background: rgba(0, 0, 0, 0.8);
      }
    }
  }

  // tablet and desktop
  @media (min-width: $sm) {
  }

  // mobile
  @media (max-width: $sm) {
    .product-nav {
      flex-direction: column;
    }
  }

  .stories {
    padding: 40px 0;

    h3 {
      text-transform: uppercase;
      padding-bottom: 5px;
    }
    img {
      max-width: 100%;
    }

    .header-stories {
      text-transform: uppercase;
      font-size: 20px;
    }
    .times {
      color: $comment-color;
      font-size: 16px;
      padding-top: -5px;
      padding-bottom: 5px;
    }
  }

  .info-block {
    padding: 60px 0;
    background-color: $main-bg;
    .info-icon {
      border-right: 1px solid $img-wrapper-shadow;
      border-left: 1px solid $img-wrapper-shadow;
      text-align: center;
      i {
        font-size: 60px;
      }
    }
  }
}
